import XLSX from 'xlsx'
import { Message } from 'element-ui'
import dayjs from 'dayjs'
import GLOBAL from './global'
/*
 * :,n
 * :
 * @method debounce
 * @param {funciton} fn   
 * @param {Number} wait   ,
 * @param {Boolean} throttle , false,
 * @returns {undefined}
 */
export const debounce = function (
    fn: (...params: any) => any | void,
    wait: number,
    throttle = false
) {
    let timeout: number | null
    return function () {
        const context = this
        // eslint-disable-next-line prefer-rest-params
        const args: any = arguments
        if (throttle) {
            //
            if (!timeout) {
                timeout = setTimeout(function () {
                    timeout = null
                    fn.apply(context, args)
                }, wait)
            }
        } else {
            //
            if (timeout) clearTimeout(timeout)
            timeout = setTimeout(function () {
                fn.apply(context, args)
            }, wait)
        }
    }
}

/**
 * 
 * @param columns antd columns
 * @param data antd table dataSource
 * @param fileName 
 * @param omit field
 */
export const exportExcel = (
    columns: any[],
    data: any[],
    fileName: string,
    ...omit: string[]
) => {
    if (!data?.length) {
        Message({
            message: '',
            type: 'warning'
        })
        return
    }

    const keys: string[] = [] // id
    // id: label
    const keyMap = columns.reduce((obj: any, cur: any) => {
        if (cur.noShow || (omit && omit.includes(cur.id))) return obj
        keys.push(cur.id)
        return { ...obj, [cur.id]: cur.label }
    }, {})

    const newData = data.reduce((arr: any[], cur: any) => {
        const newObj = keys.reduce((newCur: any, key: string) => {
            const column = columns[GLOBAL.indexOfColumn(columns, key)]
            let val: any =
                cur[key] === undefined || cur[key] === null ? '' : cur[key]
            if (column.formType === 'select' && val !== '') {
                // 
                val = column.formAttr.options.find(
                    (item: any) => item.value === val.toString()
                ).label
            } else if (column.timeFormat && val) {
                // 
                val = dayjs(val).format(column.timeFormat)
            }
            return {
                ...newCur,
                [keyMap[key]]: val
            }
        }, {})
        arr.push(newObj)
        return arr
    }, [])

    const worksheet = XLSX.utils.json_to_sheet(newData)
    const workbook = XLSX.utils.book_new()
    XLSX.utils.book_append_sheet(workbook, worksheet)
    XLSX.writeFile(
        workbook,
        `${fileName || ''}_${dayjs().format('x')}.xls`,
        {
            bookType: 'xls', // 
            bookSST: false, // Shared String TableIOS
            type: 'base64' // 
        }
    )
}
